:root {
  --color-font: $color-font;
  --size: 3rem;
}

*,
*::before,
*:after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  margin-bottom: 1em;
}

h1:last-child, h1:last-of-type,
h2:last-child,
h2:last-of-type,
h3:last-child,
h3:last-of-type,
h4:last-child,
h4:last-of-type,
p:last-child,
p:last-of-type {
  margin-bottom: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 3em 1.5em;
  margin: 0;
  background: #010101;
  color: rgba(255, 255, 255, 0.75);
  font-family: "Trueno", "sans-serif";
  width: 100vw;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
}

header {
  width: 100%;
}

header img {
  max-width: 250px;
  margin: 0 auto;
}

.language {
  max-width: 300px;
  text-align: center;
  margin-bottom: 1.5em !important;
  line-height: 1.5;
}

.flags-container {
  margin: 0 auto;
}

.flags-container a {
  display: block;
  text-align: center;
  text-transform: uppercase;
  line-height: 2em;
  font-family: "Kohinoor Bangla", "sans-serif";
  font-size: 1.5em;
}

.ageContainer {
  --color-border: #676767;
}

.ageContainer p {
  margin: 0;
}

.ageContainer .title {
  text-align: center;
  font-size: 1em;
  margin-bottom: 1rem;
  font-family: Trueno-light, "sans-serif";
  text-transform: uppercase;
}

.ageContainer .subTitle,
.ageContainer .textFooter {
  text-align: center;
  font-size: 0.9em;
}

form {
  width: 100% !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3em 0;
  margin-bottom: 1rem;
  position: relative;
}

form::after {
  content: '';
  position: absolute;
  width: 80%;
  height: 2px;
  background: var(--color-border);
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (max-width: 1024px) {
  form::after {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  form {
    padding: 10vh 0;
  }
}

form .btn {
  display: table;
  text-transform: uppercase;
  border: 1px solid var(--color-border);
  background: transparent;
  color: inherit;
  line-height: 2.5em;
  padding: 0 1em;
  font-size: 0.75em !important;
  transform: translateY(2em);
}

.form-container {
  --size: 2rem;
  width: calc((var(--size) * 8) + (var(--size) * 0.5));
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-group {
  margin-right: calc(var(--size) * 0.25);
}

.input-group:last-child, .input-group:last-of-type {
  margin-right: 0 !important;
}

.input-group.four {
  width: calc(var(--size) * 4);
}

.input-group.tow {
  width: calc(var(--size) * 2);
}

.input-container {
  position: relative;
}

.input-container::after {
  content: '';
  position: absolute;
  background: var(--color-border);
  height: 100%;
  width: 1px;
  top: 0;
  left: calc(var(--size) * 1);
}

.input-container.space-4::after {
  box-shadow: calc(var(--size) * 1) 0 var(--color-border), calc(var(--size) * 2) 0 var(--color-border);
}

label {
  font-size: calc(var(--size) * .3);
  display: block;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  padding-bottom: .5rem;
  color: var(--color-border);
}

input {
  width: 100%;
  height: var(--size);
  padding: 0 0 0 calc(var(--size) * 0.3) !important;
  letter-spacing: calc(var(--size) * 0.6);
  font-size: calc(var(--size) * 0.7);
  background: transparent;
  border: 1px solid var(--color-border);
  color: var(--color-border);
}

@media screen and (min-width: 1024px) {
  body {
    font-size: 20px;
    padding: 5vh 1.5em;
  }
  header {
    width: 100%;
  }
  header img {
    max-width: 350px;
    margin: 0 auto;
  }
  .language {
    max-width: 100%;
  }
  .flags-container {
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }
  .flags-container a {
    margin-right: 2em;
  }
  .flags-container a:last-child {
    margin-right: 0;
  }
  .ageContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .title {
    font-size: 1.3em !important;
  }
  .subTitle,
  .textFooter {
    font-size: 0.8em !important;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  .form-container {
    --size: 3rem;
  }
}

.check-group {
  display: flex;
  margin-top: 1em;
}

.check-group input {
  width: 1rem;
  height: 1rem;
  margin-right: .5rem;
}
